import React from 'react';
// import PropTypes from 'prop-types';
import { getTrending } from "../../services/movies-api";
import { useState, useEffect } from 'react';
import GalleryItem from '../../components/GalleryItem/GalleryItem';
import PageTitle from '../../components/PageTitle/PageTitle';
import default_backdrop from "../../images/default_backdrop.jpg";

// HomePage.propTypes = {
  
// };

function HomePage({pageNumber, incrementPageNumber, fetching, toggleFetching, totalPages, setTotalPages, history}) {
  const [movies, setMovies] = useState([]);
  // const [currentPage, setCurrentPage] = useState(1);
  // const [fetching, setFetching] = useState(true);
  // const [totalPages, setTotalPages] = useState(0)
  const { push, location } = history; 
  // const moviesListRef = document.getElementById('moviesList')

  useEffect(() => {
    console.log('didUpdate HomePage fetching :>> ', fetching);
    if (fetching) {
      getTrending(pageNumber).then((response) => {
        setMovies([...movies, ...response.results]);
        incrementPageNumber(); // setCurrentPage(prevPage => prevPage + 1);
        setTotalPages(response.total_results);
        // console.log('scrolling');
      }).finally(
        // console.log('scrolling');
        toggleFetching(false)//  () => setFetching(false)
      )
    }
    //  getTrending(pageNumber).then((response) => {
    //     setMovies([...movies, ...response.results]);
    //     incrementPageNumber(); // setCurrentPage(prevPage => prevPage + 1);
    //     setTotalPages(response.total_results);
    //     // console.log('scrolling');
    //   })
    // eslint-disable-next-line
  }, [fetching]);
  
  useEffect(() => {
    document.documentElement.scrollTop = 0;
    console.log('HomePage didMount', document.documentElement);
    // console.log('documentElement.scrollHeight :>>',document.documentElement.scrollHeight);
    // console.log('window.innerHeight :>> ', window.innerHeight);
    // console.log('document.documentElement.scrollTop :>> ', document.documentElement.scrollTop);
    // console.log('document.querySelector(moviesList) :>> ', document.querySelector('.moviesList'));
     
    // document.addEventListener('scroll', scrollHandler);
    // return function () {
    //   document.removeEventListener('scroll', scrollHandler)}
    // eslint-disable-next-line
 }, [])
  
  // useEffect(() => { },[])
  
//   const scrollHandler = (e) => {
//     console.log('scrolling');
//     console.log(
//       'documentElement.scrollHeight :>>', document.documentElement.scrollHeight,
//       'window.innerHeight :>> ', window.innerHeight,
//       'document.documentElement.scrollTop :>> ', document.documentElement.scrollTop);
    
//     let differenceHeight = e.target.documentElement.scrollHeight - (e.target.documentElement.scrollTop + window.innerHeight);
//     console.log('differenceHeight:', differenceHeight);
//     if (e.target.documentElement.scrollHeight - (e.target.documentElement.scrollTop + window.innerHeight) < 100 && pageNumber < totalPages) {
//      toggleFetching(true) // setFetching(true);
// }   
//   }

   const getLocation = (pathname) => ({
  state: {
      from: location,
    },
    pathname,
})

    const openMovieDetailsPage = (movieID) =>
    push(getLocation("/movies/" + movieID));
  
        // .catch((error) => console.log(error.message))
  return (
    <>
      <PageTitle text="Trending today" />
    
      <ul className="moviesList" id="moviesList">
        { movies &&
          movies.map(({ id, backdrop_path, original_title, vote_average, release_date }) =>
            {let backdrop=backdrop_path ? `https://image.tmdb.org/t/p/w780/${backdrop_path}`: default_backdrop;
            return <GalleryItem
              key={id}
              backdrop={backdrop}
              title={original_title}
              rating={vote_average}
              releaseDate={release_date}
              movieID={id}
              cbOnClick={openMovieDetailsPage}
            />}
          )
          }
        </ul>
      </>
  );
}

export default HomePage;